import React from "react";
import styled, {css} from "styled-components";

//test1 고정 스타일링
const StyledButton = styled.button`
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid lightgray;
  color: gray;
  background-color: white;
`;
function Button({ children }) {
  return <StyledButton>{children}</StyledButton>;
} 
 
  //test2 가변 스타일링1
const StyledButton2 = styled.button`
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid lightgray;

  color: ${(props) => props.color || "gray"};
  background-color: ${(props) => props.background || "white"};
`;

function Button2({ children, color, background }) {
   return (
      <StyledButton2 color={color} background={background} >
        {children}
      </StyledButton2>
    );
  }
  //test3 가변 스타일링2
  const StyledButton3 = styled.button`
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid lightgray;

  
  //props에 primary 속성값 있으면  && 다음의 css 적용(없으면 적용안함)
  //css import 선언 필요!!!
  ${(props) =>
    props.primary &&
    css`
    color: white;
    background-color: navy;
    border-color: navy;
    `}
    `;

function Button3({ children, ...props }) {
  return <StyledButton3 {...props}>{children}</StyledButton3>;
}

export default Button;
export {Button2, Button3};
