 //방법3
import React from "react";
class Params extends React.Component{
    constructor(props){//props매개변수를 전달받아 super클래스에 전달해야 props로 설정됨.
       super(props);
    }
    render(){//props 사용시 this.props.xxxx 주의!!!
        return (<div><h1>나의 {this.props.value}번째 리액트 예제 성공(클래스선언)</h1></div>)

    }
}

