import React,{useState} from "react";//User 클래스일 때

class State_User extends React.Component{
    constructor(props){
        super(props);
        this.id="User 컴포넌트의 프로퍼티변수 id";
        this.state = {id : "User 컴포넌트의 this.state객체 내의 id"}
    }

  /*  componentDidMount(){ //라이프사이클 메소드. render에 반영. 호출안해도 render 이후 자동 호출
        this.id = "this.id를 변경합니다.";
        this.setState({id : 'this.state.id를 변경합니다'});
    }
   */ 

    
  change(){ //render에서 호출해야 함. 하지만 무한루프돔. 이벤트처리와 함께 호출해야 함
    //오류this.props.id = 2000;
  //this.id = "this.id를 변경합니다.";
  //1문장씩 실행. this.id만 변경하면 state가 변화한 것이 아니므로 수정되지 않는다
  //아래 문장도 같이 실행시에는 state가 변화했으므로 this.id 도 re-render
  this.setState({id : 'this.state.id를 변경합니다'});//state가 변화했으므로 re-render
  };

    render(){


        return(<div>
           <h3 style={{color:"red"}}>this.props.id= {this.props.id} 는 불변입니다</h3>
           <h3 style={{color:"green"}}>this.id= {this.id} 는 수정 가능하고 렌더링되지 않습니다</h3>
           <h3 style={{color:"blue"}}>this.state.id= {this.state.id} 는 수정 가능하고 렌더링됩니다 </h3>
            <button onClick= {this.change.bind(this)}>{"클릭해 보세요"}</button> 
        </div>);
    }
}

function State_User2 (props){
    const [id, setId] = useState("useState의 초기값"); //배열리턴=useState("초기값")
    // [id = "useState의 초기값" , setId(i) { id = i;} ]

    let change = function(){ //render에서 호출해야 함. 하지만 무한루프돔. 이벤트처리와 함께 호출해야 함
        //오류 props.id =2000
        setId('useState의 변경값');//state가 변화했으므로 re-render
    };

    return  (<div>
           <h3 style={{color:"red"}}>props.id= {props.id} 는 불변입니다</h3>
           <h3 style={{color:"blue"}}>useState.id= {id} 는 수정 가능하고 렌더링됩니다 </h3>
           <button onClick= {change}>{"클릭해 보세요"}</button> 
        </div>);
}
export {State_User as default, State_User2}; 