import React from "react";

function Hello() {
  let msg = "나의 1번째 리액트 예제 성공";
    return (<div><h1>{msg}</h1>
    <h3>{msg}</h3></div>)
}

const Hello2  = function(p) {
    return (<div><h1>나의 {p.value}번째 리액트 예제 성공</h1></div>)
}

const Hello3=(p)=>{//const Hello=function(){}
    /*
  	화살표 함수를 이용해서 화면에 출력할 경우엔
  	render()를 사용하지 않습니다. class가 아니기 때문입니다.
    */
    /* const clickHandle=(e)=>{
        alert('함수를 이용해서 이벤트처리함!')
     }
     return(<div  onClick={clickHandle} >
     <h1>Testing2</h1>
</div>)*/

     return(<div>
                   <h1>Testing2 : {p.value} </h1>
              </div>)
   }

   class Hello4 extends React.Component{ // Hello라는 클래스를 만듭니다.
   /* constructor(p){
        super(p); //super.props = p; 자동처리; 생략가능
    }*/
    render(){ //랜더링
       const clickHandler=(e)=>{ 
          // 화살표 함수에요!! 
            //const clickHandler=function(e){ ~}
          alert('안녕하세요- 클래스 방식입니다 ')
       }
         // 클릭하면 clickHanlder 라는 이벤트가 실행된다는 markup을 리턴합니다. onClick 대소문자 주의!!!
          return(<div onClick={clickHandler}>
                       <h1>Testing!!:{this.props.value}  추가  </h1>
                 </div>)
     }//render
 }

 function Hello5(){
  let name = 'Hello5';
  const change = function(){name = name + " 변경" ; console.log(name);}
  return <h1 onClick={change}>자바스크립트 변수 사용하기 : {name} {1000+3000} {Hello()}</h1>
 }

//export default Hello;
export {Hello as default, Hello2 as H2, Hello3, Hello4, Hello5}