import React from "react";
class ParentTest extends React.Component{
    state = {
        key1 : '부모의 state key1 초기값' 
    }
    //클래스의 메소드 표현이 아니라 화살표 함수 형태의 필드 표현임
    //클래스의 메소드 표현이었거나 일반(화살표 아닌) 함수 필드였다면 bind 로 호출!!!!
    changeState = () => {
       this.setState({key1: this.state.key1 + '=> 부모가 수정'});
    }

    render(){
       return (
        <div>
            <h3>{this.state.key1}</h3>
            <button onClick = { this.changeState }>클릭버튼(페어런트의 엘리먼트)</button>
            <hr/>
	       <ChildTest key1={this.state.key1} changeState={this.changeState} />

        </div>
       )
      }
}

class ChildTest extends React.Component{
    constructor(props){
        super(props);
    //    this.state = {key1 : this.props.key1};
    }

    //changeState = () => {
    //    this.setState({key1 :this.state.key1 + "=> 자식이 수정" }); 
    //}

    render(){
        return(
        <div>
            <div>{this.props.key1}</div>
            <button onClick={this.props.changeState} >클릭버튼(차일드의 엘리먼트(부모의 메소드 호출))</button>
{/*             <div>{this.state.key1}</div>
            <button onClick={this.changeState} >클릭버튼(차일드의 엘리먼트(자식의 메소드 호출))</button> */}
        </div>
        );
      }
}
export default ParentTest;
