  //test2 arguments 전달하기
  constructor(){
    super();
    this.state = {toggle:true , result:""};
  }

  onClickToggle (name, id, e){//필요한 arguments 전달 이후 마지막에 event객체로 간주
    this.setState({
        result :  id + " 번 " + name + ":" +e.type + " 발생",
        toggle : !this.state.toggle
    });    
  }

  render(){    
    return <div>
    <button onClick={(e)=>{this.onClickToggle("토글버튼", 100, e)}}>{this.state.toggle === true?"켜짐":"꺼짐"}</button>
    <h3>{this.state.result}</h3>
    </div>}  
