function MyDom(){
    return <div><h1>나의돔</h1><ButtonDom /></div>
}

function ButtonDom(){
    return <button style={{backgroundColor:"pink"}}><p>버튼을 표현하는 React_DOM 입니다.</p></button>
}
//브라우저 개발자 도구 열어서 보면 <ButtonDom />라는 리액트 가상dom을
//<button style="backgroundColor:pink"><p>.. 라는 브라우저 실제dom
//으로 변경하는 것을 볼 수 있다.
console.log(MyDom());
export default MyDom;
