import React, {Component} from "react";//User 클래스일 때
import { useEffect, useState } from "react";//User2 함수일 때

class User extends React.Component{
    constructor(props){
        super(props);
        this.id="User 컴포넌트의 프로퍼티변수 id";
        this.state = {id : "User 컴포넌트의 this.state객체 내의 id"}
    }

    render(){
       /* let change=()=>{
            this.id= this.id + " 변경";
            console.log(this.id);
            this.setState({id : this.state.id + " 변경"});
        }*/
        return(<div>
            <h3>this.props.id= {this.props.id} 는 불변입니다</h3>
            <h3>this.id= {this.id} 는 수정 가능하나 렌더링되지 않습니다</h3>
            <h3>this.state.id= {this.state.id} 는 수정 가능하고 렌더링됩니다 </h3>
        </div>);
    }
}





let cnt = 1;
function User2 (props){
    let id= "클릭하세요";//"지역변수 id";//state의 변수명과 중복시 오류
    // const [stateid, setStateId] = useState("STATE ID");
    const myState = useState("STATE ID" + cnt);
    const stateid = myState[0];
    const setStateId = myState[1];
   let change=()=>{
       id= id + " - 변경";
       console.log(id);
            let a = stateid.slice(0, 8);
            
            //alert(a); 
            let b = a + " : " + cnt++;
            //alert(b);
            //setStateId(a);//(stateid + " 값으로 변경");
            
            setStateId(b);


            console.log("change = " + stateid);//setState 직후는 rendering 이전이므로 변경안되고 화면 렌더링된 이후에 변경됨.
                                               //따라서, useEffect 이용
    }
    //화면 렌더링 이후!!!! State객체의 stateid 변수 추적 과정 출력
    //단 useState 내의 값 배열에 대해서 . 
    useEffect(function(){console.log('useEffect = ' + stateid); }, [stateid]);
    //useEffect(function(){console.log('useEffect = ' + stateid); });

        return(<div>
            <h3>props.id= {props.id} 는 불변입니다</h3>
            <h3 onClick={change}> id= {id}  </h3>
            <h3 > stateid= {stateid + " 번 클릭"}  </h3>
        </div>);
    }

export { User as default,  User2};

