import React from "react";
import Notification from "../Notification";

const reservedNotifications = [
    { id: 1,   message: "안녕하세요, 오늘 일정을 알려드립니다.",   },
    { id: 2,   message: "점심식사 시간입니다.",  },
    { id: 3,   message: "이제 곧 미팅이 시작됩니다.", },
];

var timer;

class MyNotificationList extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            notifications: [],
        };
    }

    componentDidMount() {
        const { notifications } = this.state;
        timer = setInterval(() => {
            if (notifications.length < reservedNotifications.length) { // 1초에 1개씩 일정 증가
                const index = notifications.length;
                notifications.push(reservedNotifications[index]);
                this.setState({
                    notifications: notifications,
                });
            } else { //3개 다 차면 비워라.
                this.setState({
                    notifications: [],
                });
                //clearInterval(timer);
            }
        }, 1000);
    }

    componentWillUnmount() {//상위컴포넌트에서 더이상 현재 컴포넌트를 화면에 표시하지 않을 때 호출. 3개 다 그렸으므로.
        if (timer) {
            clearInterval(timer);
        }
    }

    render() {
        return (
            <div>
                {this.state.notifications.map((notification) => {
                    return (
                        <Notification
                            key={notification.id}
                            id={notification.id}
                            message={notification.message}
                        />
                    );
                })}
            </div>
        );
    }
}

export default MyNotificationList; 
