import React , {useState}from "react"; 
import { useContext } from "react";
//import ThemeContext from "./chapter_14/ThemeContext";
class BoardWriteForm extends React.Component{ 
    constructor(props){
        super(props);
        this.state={
            board : props.contents, 
            seq :  props.contents.length+1,//현재글의 갯수 
            title : '', //'추가제목',
            writer : '', //'추가작성자',
            view : 0 //조회수
            
        };
        
    }//constructor end
    render(){
        const clickHandler=(event)=>{
            //this.setState({seq : this.state.seq+1 , view : this.state.view+1});//새글번호(현재글의 갯수+1)
           // this.setState({view : this.state.view+1});//새글작성시 조회수 증가하려면 포함
            //this.setState(
            // ()=>{
            //     this.state.board.push(
            //         {seq:this.state.seq , title:this.state.title, 
            //            writer : this.state.writer, view:this.state.view}) }  );
            
            alert(this.state.seq +":" + this.state.title
            + ":" + this.state.writer +":" +this.state.view);
            //alert(this.state.board.length);
         
            this.props.addBoard( {seq:this.state.seq , title:this.state.title, 
                writer : this.state.writer, view:this.state.view} );
         
             }
        const changeHandler=(event)=>{
                this.setState({[event.target.id] : event.target.value});
            }
        return (
           <div>
                
            게시물제목:<input type="text" id="title" onChange={changeHandler} />
            게시물작성자:<input type="text" id="writer" onChange={changeHandler} />
            <button  onClick={clickHandler} >글쓰기</button>
          </div>
        );
    }//render end
}//class end
class BoardHeading extends React.Component{
    render(){
        return(<thead><tr><th>번호</th><th>제목</th><th>작성자</th><th colSpan="2">추가동작</th></tr></thead>);
    }
}
class Board extends React.Component{

    constructor(props){
        super(props);
        //alert("Board생성자")

        this.state = {isUpdateClick : false , updateSeq:0, updateTitle : '', updateWriter:''};
    }
    render(){
        //alert("Board렌더");
        //alert(this.constructor == Board);

        const deleteItem = (e)=>{
           // alert("삭제확인");
           // alert(this.constructor == Board);
            //화살표함수에서 this는 이벤트발생 객체가 아닌 함수 포함 객체로 현재는 Board이다. 
            //화살표함수는 this가 동적이 아니라 정적으로 고정되어 있다.
            //이벤트발생객체는 e.target 으로 표현
            this.props.deleteBoard(e.target.id);//string으로 전달
        }

        //수정버튼 클릭시 클릭한 글의 id 읽고, isUpateClick true로 변경
         const updateItem = (e)=>{
            alert("수정확인");

            this.setState({isUpdateClick : true, updateSeq:e.target.id});
         }

         //제목, 작성자 수정글 저장
        const updateHandler = (e)=>{
            //alert("수정확인");
            this.setState({[e.target.name] :e.target.value });
         }
         //수정완료 버튼 클릭시 게시물에 수정 반영 후 isUpateClick 다시 false로 변경
         const updateEndItem = (e)=>{
            const updateboard = {seq:this.state.updateSeq, title:this.state.updateTitle, writer:this.state.updateWriter} 
            console.log(updateboard);
            this.props.updateBoard(updateboard );
            this.setState({isUpdateClick : false});
         }

       const board_tr = this.props.contents.map( (oneboard, index)=>{ 
                        return (<tr key={oneboard.seq}>
                            <td>{oneboard.seq}</td>
                            <td>{oneboard.title}</td>
                        <td>{oneboard.writer}</td>
{/* 
                        {
                        this.state.isUpdateClick && this.state.updateSeq == oneboard.seq 
                        ? 
                        <td><input name='updateTitle' onChange={updateHandler} />
                        <input name='updateWriter' onChange={updateHandler} />
                        <button id={oneboard.seq} onClick={updateEndItem} className="btn btn-danger">수정완료</button>
                        </td>
                        :
                        <td><button id={oneboard.seq} onClick={deleteItem} className="btn btn-danger">삭제</button>
                        <button id={oneboard.seq} onClick={updateItem} className="btn btn-danger">수정</button></td>
                        } */}
                        </tr>)
      }
      );//map end
       //하나의 태그로 리턴해야 함
       return(<tbody>{board_tr}</tbody>);
    }
}

